import {SyncOutlined, RightOutlined, DownOutlined} from "@ant-design/icons";
import {BigNumber, ethers, utils} from "ethers";
import {Button, Card, DatePicker, Menu, Divider, Input, notification, Progress, Slider, Spin, Switch, Dropdown} from "antd";
import React, {useState} from "react";
import {Address, Balance, Events} from "../components";
import {useContractReader} from "eth-hooks";

export default function OrcsUI({
                                   address,
                                   mainnetProvider,
                                   userSigner,
                                   localProvider,
                                   yourLocalBalance,
                                   tx,
                                   readContracts,
                                   writeContracts,
                               }) {
    const priceWei = useContractReader(readContracts, "LazyOrcs", "price");
    const nWhitelistMints = parseInt(useContractReader(readContracts, "LazyOrcs", "nWhitelistMints",[address]), 10);
    const nFreeMints = parseInt(useContractReader(readContracts, "LazyOrcs", "nFreeMints", [address]), 10);

    async function handleMenuClick(e) {
        const mintNOrcs = parseInt(e.key);
        if (mintNOrcs > 5 || mintNOrcs <= 0) {
            notification.error({
                message: "Number Error",
                description: "The provided number of NFTs has to be between 1 and 5 (inclusive).",
            });
        } else {
            await tx(writeContracts.LazyOrcs.mintOrc(mintNOrcs, {value: priceWei.mul(mintNOrcs)}));
        }
    }

    const menu = (
        <Menu onClick={handleMenuClick}>
            <Menu.Item key="1" icon={<RightOutlined/>}>1 Orc</Menu.Item>
            <Menu.Item key="2" icon={<RightOutlined/>}>2 Orcs</Menu.Item>
            <Menu.Item key="3" icon={<RightOutlined/>}>3 Orcs</Menu.Item>
            <Menu.Item key="4" icon={<RightOutlined/>}>4 Orcs</Menu.Item>
            <Menu.Item key="5" icon={<RightOutlined/>}>5 Orcs</Menu.Item>
        </Menu>
    );

    return (
        <div>
            <div style={{border: "1px solid #cccccc", padding: 16, width: 400, margin: "auto", marginTop: 64}}>
                <h2>Lazy Orcs</h2>
                <Divider/>
                <div style={{margin: 8}}>
                    <Dropdown overlay={menu}>
                        <Button>
                            MINT Orcs for {priceWei ? utils.formatEther(priceWei) : "..."} ether each <DownOutlined/>
                        </Button>
                    </Dropdown>
                </div>
                <Divider/>
                <div style={{margin: 8}}>
                    <Button
                        disabled={nWhitelistMints === 0}
                        onClick={() => {
                            tx(writeContracts.LazyOrcs.whitelistMintOrc({value: priceWei}));
                        }}
                    >
                        Whitelist mint ({nWhitelistMints} remaining)
                    </Button>
                </div>
                <div style={{margin: 8}}>
                    <Button
                        disabled={nFreeMints === 0}
                        onClick={() => {
                            tx(writeContracts.LazyOrcs.freeMintOrc());
                        }}
                    >
                        Free mint ({nFreeMints} remaining)
                    </Button>
                </div>
                <Divider/>
                Contract address:&nbsp;
                <Address
                    address={readContracts && readContracts.LazyOrcs ? readContracts.LazyOrcs.address : null}
                    ensProvider={mainnetProvider}
                    fontSize={16}
                />
            </div>
        </div>
    );
}
